import AccountTreeIcon from '@mui/icons-material/AccountTree';
import LinkIcon from '@mui/icons-material/Link';
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';
import TextSnippetIcon from '@mui/icons-material/TextSnippet';
import { AppBar, Container, CssBaseline, Toolbar, Tooltip, Typography } from '@mui/material';
import { DataGrid, GridActionsCellItem } from '@mui/x-data-grid';
import { FC, useEffect, useState } from 'react';

type ActionType = 'supervisor' | 'doc' | 'link';

type Action = {
  type?: ActionType;
  label: string;
  icon: any;
  link?: string;
};

type Host = {
  address: string;
  port: string;
};

type Service = {
  id: number;
  name: string;
  ip: string;
  actions: Action[];
  hosts: Host[];
}

type Data = {
  title: string;
  ip: string;
  services: Service[];
}

const getActions = (service: Service, ip: string) => {
  const actions: Action[] = [];

  service.actions.forEach((a: Action) => {
    const action: Partial<Action> = {
      label: a.label,
      link: a.link,
    };

    switch (a.type) {
      case 'doc':
      action.icon = <TextSnippetIcon />;
      break;

      case 'link':
      action.icon = <LinkIcon />;
      break;
    }

    actions.push(action as Action);
  });

  actions.push({
    label: 'Supervisor',
    icon: <AccountTreeIcon />,
    link: `http://${ip}:9090`,
  });

  actions.push({
    label: 'View Detail',
    icon: <RemoveRedEyeIcon />,
  });

  return actions;
}

const MainPage: FC = () => {
  const [title, setTitle] = useState('Dock<er');
  const [ip, setIp] = useState('0.0.0.0');
  const [services, setServices] = useState<Service[]>([]);

  useEffect(() => {
    fetch('/data.json')
      .then((response: any) => {
        if (response.ok) {
          return response.json();
        }
      })
      .then((jsonData: Data) => {
        setIp(jsonData.ip);
        setTitle(jsonData.title);
        setServices(jsonData.services);
      })
    }, []);

  return (
    <>
      <CssBaseline />
      <AppBar position='fixed'>
        <Toolbar>
          <Typography
            variant="h2"
            sx={{ flexGrow: 1, display: { xs: 'none', sm: 'block' } }}
          >
            {title}
          </Typography>
        </Toolbar>
      </AppBar>

      <Container maxWidth={'md'} sx={{ marginTop: 14 }}>
      <DataGrid
        columnHeaderHeight={25}
        columns={[
          { field: 'project', headerName: 'Project', disableColumnMenu: true, sortable: false,  width: 300 },
          { field: 'ip', headerName: 'IP Container', disableColumnMenu: true, sortable: false, width: 150, headerAlign: 'center', align: 'center' },
          {
            field: 'actions',
            type: 'actions',
            headerName: 'Ações',
            width: 300,
            cellClassName: 'actions',
            headerAlign: 'center',
            align: 'right',
            getActions: ({ id, row: service }) => [
              ...getActions(service, ip).map((action: Action) =>
              <Tooltip title={action.label}>
                <GridActionsCellItem icon={action.icon} label={action.label}  color='inherit' />
              </Tooltip>
              ),
            ],
          }
        ]}
        rowHeight={25}
        rowSelection={false}
        hideFooterPagination={true}
        rows={services ?? []}
      />
      </Container>
    </>
  );
}

export default MainPage;
